import React from 'react'

import '../styles/Morty.css';

export const Search = ({search, searchInput, handleSearch}) => {
    return (
        <div className="col-12 mx-auto">
            <input  className="SearchRyM mx-auto mt-4" type="text" value={search} ref={searchInput} onChange={handleSearch} placeholder="Busca tu personaje..."  />
        </div>
    )
};
export default Search;
