
import React, {useMemo, useReducer, useState, useRef, useCallback } from "react";
import useCharacter from "../hooks/useCharacter";
import { Card } from "react-bootstrap";
import Search from "./Search";
import '../styles/Morty.css';



const initialState = { favorites: [] };

const favoritesReducer = (state, action) => {
    switch (action.type) {
        case 'ADD_TO_FAVORITE':
            return {
                ...state,
                favorites: [ ...state.favorites, 
                            action.payload ]
            }
        case 'DELETED_TO_FAVORITE':
            return {
                ...state,
                favorites: state.favorites.filter(fa => fa.id !== action.payload)
            }
        default:
            return state;
    }
};

const API = 'https://rickandmortyapi.com/api/character/'

const Characters = () => {

    const [favorites, dispatch] = useReducer(favoritesReducer, initialState);
    const [search, setSearch] = useState('');
    const searchInput = useRef(null);

    const characters = useCharacter(API);

    const filteredUsers = useMemo(() =>
        characters.filter((user) => {
            return user.name.toLowerCase().includes(search.toLowerCase());
        }),
        [characters, search]
    );

    const handleSearch = useCallback(() => {
        setSearch(searchInput.current.value);
    },[]);

    const handleClick = favorite => {
        dispatch({
            type: 'ADD_TO_FAVORITE',
            payload: favorite
        });
    }

    const handleDelete = favorite => {
        dispatch({
            type: 'DELETED_TO_FAVORITE',
            payload: favorite
        });
    }

    return (
        <div className="container">
                <Search 
                    search={search} 
                    searchInput={searchInput} 
                    handleSearch={handleSearch}
                />
            <div className="row">
                {favorites.favorites.map(favorite => (
                    <li className="  cardMorty row mt-5 m-4 mx-auto" key={favorite.id}>
                        <img className=" imgCardFav" variant="top" src={favorite.image} />
                        <i className="btnCorazon mx-auto  mb-4  fas fa-times" onClick={() => handleDelete(favorite.id)}></i>
                    </li>
                ))}
            </div>

            <hr />

            <div className="row">
                {filteredUsers.map((character) => (
                    <Card className=" cardMorty row mt-5 m-4 mx-auto" key={character.id}>
                        <Card.Img className=" imgCard" variant="top" src={character.image} />
                        <Card.Body className="textCard" >
                            <Card.Title>{character.name}</Card.Title>
                            <Card.Text >
                                <div>{character.species}</div>
                                <div>{character.status}</div>
                                <div>{character.gender}</div>
                                <i className=" btnCorazon fas fa-heart mt-4" onClick={() => handleClick(character)}></i>
                            </Card.Text>
                        </Card.Body>
                    </Card>
                ))}
            </div>

        </div>
    );
};

export default Characters;